import React from 'react';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';
import Icon24ShareOutline from '@vkontakte/icons/dist/24/share_outline';
import Icon24Advertising from '@vkontakte/icons/dist/24/advertising';
// import styles from "./App.module.sass";
import styles from "../styles/custom-styles.sass";
import Fake from 'react-reveal';

const Favorites = ({name, photo, year, idItem, storyFavorites, share}) => {

    const shareIcon1 =
        <Div id={idItem} onClick={storyFavorites}>
            <Icon24ShareOutline  fill="var(--accent)"/>
        </Div>;
    // const shareIcon2 =
    //     <Div id={idItem} onClick={share}>
    //         <Icon24Advertising  fill="var(--accent)"/>
    //     </Div>;

    return (
        <Fake top>
            <Div className='card bg-color'>
                <Cell
                    className='myItem'
                    id={idItem}
                    description={year}
                    before={<Avatar
                        type="image"
                        src={photo}
                        size={100}
                    />}
                    asideContent={shareIcon1}
                    // indicator={shareIcon2}
                >
                    {name}
                </Cell>
            </Div>
        </Fake>
    )
};


export default Favorites;


