        function storyFavorites(e) {
            e.preventDefault();
            let url = "https://vk.com/app7254171";
            const targetId = +e.currentTarget.id;
            // const targetPhoto = dataStorage[targetId].photo;
            const findTargetId = dataStorage.find(
                item => item.id === targetId
            );
            const findTargetIdPhoto = findTargetId.photo;

            console.log("targetId:", targetId);
            console.log("findTargetId:", findTargetId);
            console.log("findTargetIdPhoto:", findTargetIdPhoto);

            connect.sendPromise('VKWebAppShowStoryBox', {
                "background_type": "image",
                "url": findTargetIdPhoto,
                "attachment": {
                    "text": "go_to",
                    "type": "url",
                    "url": url
                }
            }).then(res => {
                console.log("story res:", res);
            }).catch(error => {
                console.log("story error:", error);
            });
        }

